import { Box, Button, Typography } from '@mui/material';
import { CheckOutlined } from '@mui/icons-material';
import { useState } from 'react';

type TagColorPickerProps = {
  callback: (color: string) => void;
  color: string;
};

const MuiColors = ['#f44336', '#e91e63', '#9c27b0', '#673ab7'];

export const TagColorPicker = (props: TagColorPickerProps) => {
  const { callback, color } = props;
  const [activeColor, setActiveColor] = useState<string>(color);

  const changeActiveColor = (newColor: string) => {
    setActiveColor(newColor);
    callback(newColor);
  };

  return (
    <>
      <Typography variant='caption'>Vlj frg</Typography>
      <Box
        sx={{
          mb: 2,
          display: 'grid',
          gridTemplateColumns: 'repeat(4, 1fr)',
          gridTemplateRows: '2rem',
          gridGap: '1px',
          '& .MuiButton-root': {
            minWidth: 'auto',
          },
        }}
      >
        {MuiColors.map((item) => {
          return (
            <Button
              key={item}
              sx={{
                color: 'white',
                backgroundColor: `${item} !important`,
                borderRadius: 0,
              }}
              disableElevation
              onClick={() => changeActiveColor(item)}
            >
              {item === activeColor && <CheckOutlined />}
            </Button>
          );
        })}
      </Box>
    </>
  );
};
