import { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
  TextField,
  Typography,
} from '@mui/material';
import { TickList } from 'src/database/models/tickList';
import { ListTypeEnum } from 'src/shared/enums/listTypeEnum';
import { db } from 'src/database';
import { toast } from 'react-toastify';
import { EditTickListTags } from './editTickListTags';

export type EditTickListDialogProps = {
  tickList: TickList | null;
  value: string | null;
  open: boolean;
  onClose: (value?: any) => void;
};

export const EditTickListDialog = (props: EditTickListDialogProps) => {
  const { tickList, onClose, value: valueProp, open, ...other } = props;

  const [listName, setListName] = useState<string>(tickList?.listName || 'Lista');
  const changeListName = (event: React.ChangeEvent<HTMLInputElement>) => {
    setListName(event.target.value as string);
  };

  const [listType, setListType] = useState<ListTypeEnum>(tickList?.listType || ListTypeEnum.Custom);
  const changeListType = (event: SelectChangeEvent) => {
    setListType(event.target.value as ListTypeEnum);
  };

  const dialogId = `editticklist-dialog-title-${Date.now().toString(36) + Math.random().toString(36).substring(2)}`;

  const handleCancel = () => {
    onClose();
  };

  const handleOk = () => {
    if (tickList?.id) {
      db.tickList
        .update(tickList.id, {
          listName,
          listType,
        })
        .then(() => {
          toast.success('Listan uppdaterades');
          onClose({ id: props.value });
        });
    } else {
      db.tickList
        .add({
          listName,
          listType,
          tickCount: 0,
          ticks: [],
          created: new Date(),
          updated: new Date(),
          favourite: false,
        } as unknown as TickList)
        .then(() => {
          toast.success('Listan skapades');
          onClose({ id: null });
        });
    }
  };

  return (
    <Dialog onBackdropClick={() => false} disableEscapeKeyDown aria-labelledby={dialogId} open={open} {...other}>
      <DialogTitle id={dialogId}>Redigera artlista</DialogTitle>
      <DialogContent>
        <TextField fullWidth label='Listans namn' variant='outlined' size='small' value={listName} onChange={changeListName} sx={{ mt: 3 }} />
        <FormControl fullWidth sx={{ my: 3 }}>
          <InputLabel id='demo-simple-select-label'>Typ av lista</InputLabel>
          <Select
            labelId='demo-simple-select-label'
            size='small'
            id='demo-simple-select'
            value={listType}
            label='Typ av lista'
            onChange={changeListType}
          >
            <MenuItem value={ListTypeEnum.Landskap}>{ListTypeEnum.Landskap}</MenuItem>
            <MenuItem value={ListTypeEnum.Kommun}>{ListTypeEnum.Kommun}</MenuItem>
            <MenuItem value={ListTypeEnum.Lokal}>{ListTypeEnum.Lokal}</MenuItem>
            <MenuItem value={ListTypeEnum.Custom}>{ListTypeEnum.Custom}</MenuItem>
          </Select>
        </FormControl>
        {tickList && <EditTickListTags tickListId={tickList.id} />}
      </DialogContent>
      <DialogActions>
        <Button autoFocus onClick={handleCancel} color='primary' sx={{ mr: 1 }}>
          Avbryt
        </Button>
        <Button onClick={handleOk} color='primary' variant='contained'>
          Spara
        </Button>
      </DialogActions>
    </Dialog>
  );
};
