import { Player, GameState, Status, initialGameState, SortTickListEnum } from './state';
import {
  ActionType,
  AddPlayer,
  FocusQuantity,
  FocusSpeciesName,
  GameActions,
  ResetGame,
  SetPlayerValue,
  SetActiveTag,
  ToggleListView,
  ToggleListDrawer,
  SortTickList,
  ExpandedTickListType,
} from './actions';
import { ListTypeEnum } from '../enums/listTypeEnum';
import ConsoleHelper from '../helpers/consoleHelper';

export function gameReducer(state: GameState, action: GameActions): GameState {
  ConsoleHelper.log('gameReducer', state, action);
  switch (action.type) {
    case ActionType.ToggleListView:
      return { ...state, listViewType: !state.listViewType };
    case ActionType.ToggleListDrawer:
      return { ...state, listDrawerVisible: !state.listDrawerVisible };
    case ActionType.SortTickList:
      return { ...state, sortListAsc: !state.sortListAsc, sortListBy: action.payload.sortBy };
    case ActionType.ExpandedTickListType:
      return { ...state, expandedTickListType: action.payload.expandedTickListType };
    case ActionType.SetActiveTag:
      return { ...state, activeTagId: action.payload.activeTagId };
    case ActionType.FocusQuantity:
      return { ...state, focusQuantity: true, focusSpeciesName: false };
    case ActionType.FocusSpeciesName:
      return { ...state, focusQuantity: false, focusSpeciesName: true };
    case ActionType.AddPlayer:
      return { ...state, players: [action.payload, ...state.players] };
    case ActionType.ResetGame:
      return {
        ...initialGameState,
        players: state.players.map((player) => ({
          ...player,
          status: Status.NotStarted,
          value: 0,
        })),
      };

    default:
      return state;
  }
}

const getWinner = (players: Player[]): Player | null => {
  let winnerValue = 0;
  let winner = null;
  players.forEach((player) => {
    if (player.value && player.value > winnerValue) {
      winner = player;
      winnerValue = player.value || 0;
    }
  });
  return winner;
};

// helper functions to simplify the caller
export const addPlayer = (player: Player): AddPlayer => ({
  type: ActionType.AddPlayer,
  payload: player,
});

export const setPlayerValue = (id: number, value: number): SetPlayerValue => ({
  type: ActionType.SetPlayerValue,
  payload: { id, value },
});

export const setListSort = (sortBy: SortTickListEnum): SortTickList => ({
  type: ActionType.SortTickList,
  payload: { sortBy },
});

export const setExpandedListType = (expandedTickListType: ListTypeEnum): ExpandedTickListType => ({
  type: ActionType.ExpandedTickListType,
  payload: { expandedTickListType },
});

export const setActiveTagId = (activeTagId: number | null): SetActiveTag => ({
  type: ActionType.SetActiveTag,
  payload: { activeTagId },
});

export const resetGame = (): ResetGame => ({
  type: ActionType.ResetGame,
});

export const toogleListViewType = (): ToggleListView => ({
  type: ActionType.ToggleListView,
});

export const toogleListDrawer = (): ToggleListDrawer => ({
  type: ActionType.ToggleListDrawer,
});

export const focusQuantity = (): FocusQuantity => ({
  type: ActionType.FocusQuantity,
});

export const focusSpeciesName = (): FocusSpeciesName => ({
  type: ActionType.FocusSpeciesName,
});
