import { HashRouter as Router } from 'react-router-dom';
import { PageRoutes } from 'src/shared/routes/pageRoutes';
import { GameContext } from 'src/shared/state/context';
import { gameReducer } from 'src/shared/state/reducer';
import { initialGameState } from 'src/shared/state/state';
import { useReducer } from 'react';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

// ...
// import { unregister } from 'src/serviceWorkerRegistration';
// unregister();

const App = () => {
  const [state, dispatch] = useReducer(gameReducer, initialGameState);

  return (
    <GameContext.Provider value={{ state, dispatch }}>
      <Router basename='/'>
        <PageRoutes />
        {/* <AppTemplate mainNavigation={<PageHeader />} mainContent={<PageRoutes />} /> */}
      </Router>
      <ToastContainer
        theme='colored'
        position='top-right'
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </GameContext.Provider>
  );
};

export default App;
