import { Collapse, Drawer, List, ListItem, ListItemButton, ListItemIcon, ListItemText, ListSubheader, Typography } from '@mui/material';
import { ExpandMore, ExpandLess } from '@mui/icons-material';
import React, { useContext, useEffect, useState } from 'react';
import { db } from 'src/database';
import { TickList } from 'src/database/models/tickList';
import { Link } from 'react-router-dom';
import { GameContext } from 'src/shared/state/context';
import { toogleListDrawer } from 'src/shared/state/reducer';

export const ListDrawer = () => {
  const { state, dispatch } = useContext(GameContext);
  const [tickLists, setTickLists] = useState<TickList[]>([]);

  const fetchData = async () => {
    await db.transaction('r', [db.tickList], async () => {
      const dbResult = await db.tickList.toArray();
      // .where('firstName').startsWithAnyOfIgnoreCase('a', 'b', 'c').sortBy('id');
      setTickLists(dbResult);
    });
  };
  useEffect(() => {
    fetchData();
  }, []);

  const [open, setOpen] = React.useState(true);

  const handleClick = () => {
    setOpen(!open);
  };

  const toggleDrawer = () => {
    dispatch(toogleListDrawer());
  };

  return (
    <Drawer anchor='top' open={state.listDrawerVisible} onClose={() => toggleDrawer()}>
      <List
        disablePadding
        sx={{ width: '100%', bgcolor: 'background.paper' }}
        component='nav'
        aria-labelledby='nested-list-subheader'
        subheader={
          <ListSubheader component='div' id='nested-list-subheader'>
            Nested List Items
          </ListSubheader>
        }
      >
        <ListItemButton onClick={handleClick}>
          <ListItemText
            primary={
              <>
                <Typography variant='h6' component='span' sx={{ fontWeight: 600 }}>
                  Kommun
                </Typography>
                <Typography component='span' sx={{ ml: 1 }}>
                  (1)
                </Typography>
              </>
            }
          />
          {open ? <ExpandLess /> : <ExpandMore />}
        </ListItemButton>
        <Collapse in={open} timeout='auto' unmountOnExit>
          <List component='div' disablePadding>
            {tickLists &&
              tickLists.map((item) => {
                return (
                  <ListItem
                    sx={{
                      pl: 4,
                    }}
                    key={item.id}
                    secondaryAction={item.tickCount}
                    onClick={() => toggleDrawer()}
                    component={Link}
                    to={`/list/${item.id}`}
                  >
                    <ListItemText primary={item.listName} />
                  </ListItem>
                );
              })}
          </List>
        </Collapse>
      </List>
    </Drawer>
  );
};
