import { ArrowDownward, ArrowUpward } from '@mui/icons-material';
import { Button } from '@mui/material';
import { useContext } from 'react';
import { GameContext } from 'src/shared/state/context';
import { setListSort } from 'src/shared/state/reducer';
import { SortTickListEnum } from 'src/shared/state/state';

export type SortListButtonProps = {
  label: string;
  sortKey: SortTickListEnum;
};

export const SortListButton = (props: SortListButtonProps) => {
  const { label, sortKey } = props;
  const { state, dispatch } = useContext(GameContext);

  const setSort = () => {
    dispatch(setListSort(sortKey));
  };

  const isActive = state.sortListBy === sortKey;

  return (
    <Button sx={{ fontWeight: isActive ? 700 : 400 }} size='small' onClick={() => setSort()}>
      {label}
      {isActive && state.sortListAsc && <ArrowUpward sx={{ pl: 0.5, fontSize: '1.5rem' }} />}
      {isActive && !state.sortListAsc && <ArrowDownward sx={{ pl: 0.5, fontSize: '1.5rem' }} />}
    </Button>
  );
};

SortListButton.defaultProps = {
  isActive: false,
  sortAscending: false,
};
