import { ExpandMore, Download, Delete, ExitToApp, Star, StarBorder, Edit, MoreVert } from '@mui/icons-material';
import { Box, Button, Divider, ListItemIcon, Menu, MenuItem, Typography } from '@mui/material';
import { db } from 'src/database';
import React from 'react';
import { toast } from 'react-toastify';
import { useHistory } from 'react-router-dom';
import { TickList } from 'src/database/models/tickList';
import { Tick } from 'src/database/models/tick';
import { DeleteConfirmationDialog } from 'src/shared/components/organisms/deleteConfirmationDialog';
import { RoutesEnum } from 'src/shared/enums/routes.enum';
import { EditTickListDialog } from './editTickListDialog';

type TickListActionMenuProps = {
  tickList: TickList;
};

export const TickListActionMenu = (props: TickListActionMenuProps) => {
  const { tickList } = props;
  const history = useHistory();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const toggleFavourite = () => {
    if (tickList !== undefined) {
      toast.success(!tickList.favourite ? 'Lades till som favorit' : 'Favorit borttagen');
      db.tickList.where('id').equals(tickList.id).modify({
        favourite: !tickList.favourite,
      });
      handleClose();
    }
  };

  const createCSV = () => {
    const csvString = [['TaxonId', 'Datum'], ...tickList.ticks.map((item: Tick) => [item.taxonId, item.date])].map((e) => e.join(',')).join('\n');

    console.log(csvString);
  };

  const gotToAp = () => {
    window.open(tickList.apUrl);
    handleClose();
  };

  const [deleteId, setDeleteId] = React.useState<string | null>(null);

  const showDeleteConfirmationDialog = () => {
    setDeleteId(`${tickList.id}`);
  };

  const handleCloseDialog = async (newValue: any) => {
    setDeleteId(newValue);

    if (newValue) {
      toast.success('Listan raderades');
      await db.tickList.where('id').equals(tickList.id).delete();
      history.push(RoutesEnum.ListOverview);
    }
  };

  const [tickListId, setTickListId] = React.useState<string | null>(null);

  const showEditTickListDialog = () => {
    setTickListId(`${tickList.id}`);
  };

  const handleCloseEditTickDialog = () => {
    setTickListId(null);
  };

  const formatDate = (inputDate: Date) => {
    return `${inputDate.toISOString().substring(0, 10)} ${inputDate.toLocaleTimeString().substring(0, 5)}`;
  };

  return (
    <>
      {tickList && <EditTickListDialog tickList={tickList} open={!!tickListId} onClose={handleCloseEditTickDialog} value={tickListId} />}
      <DeleteConfirmationDialog title='Vill du radera listan?' modaltext='' open={!!deleteId} onClose={handleCloseDialog} value={deleteId} />
      <Button
        variant='outlined'
        color='primary'
        size='small'
        sx={{
          padding: 0,
          minWidth: '2rem',
          height: '2rem',
          lineHeight: 1,
          color: 'text.secondary',
          borderColor: '#ccc',
        }}
        id='basic-button'
        aria-controls='basic-menu'
        aria-haspopup='true'
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        <ExpandMore />
      </Button>
      <Menu
        id='basic-menu'
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        <MenuItem onClick={() => showEditTickListDialog()}>
          <ListItemIcon>
            <Edit fontSize='small' />
          </ListItemIcon>
          Redigera lista
        </MenuItem>
        <MenuItem onClick={() => toggleFavourite()}>
          {tickList.favourite && (
            <>
              <ListItemIcon>
                <StarBorder fontSize='small' />
              </ListItemIcon>
              Tabort favorit
            </>
          )}
          {!tickList.favourite && (
            <>
              <ListItemIcon>
                <Star fontSize='small' />
              </ListItemIcon>
              Gr till favorit
            </>
          )}
        </MenuItem>
        {tickList.apUrl && (
          <MenuItem onClick={() => gotToAp()}>
            <ListItemIcon>
              <ExitToApp fontSize='small' />
            </ListItemIcon>
            ppna i Artportalen
          </MenuItem>
        )}
        <MenuItem onClick={() => createCSV()}>
          <ListItemIcon>
            <Download fontSize='small' />
          </ListItemIcon>
          Exportera lista (csv)
        </MenuItem>
        <MenuItem divider onClick={() => showDeleteConfirmationDialog()}>
          <ListItemIcon>
            <Delete fontSize='small' />
          </ListItemIcon>
          Radera listan
        </MenuItem>
        <MenuItem sx={{ flexDirection: 'column', justifyContent: 'flex-start', pt: 2 }}>
          <Typography variant='caption' sx={{ color: 'text.secondary' }}>
            <strong>Uppdaterad: </strong>
            {formatDate(tickList.updated)}
            <br />
            <strong>Skapad: </strong>
            {formatDate(tickList.created)}
          </Typography>
        </MenuItem>
      </Menu>
    </>
  );
};
