import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

type GenericDialogProps = {
  openDialog: boolean;
  dialogTitle?: JSX.Element;
  dialogContent: JSX.Element;
  abortLabel?: string;
  saveLabel?: string;
  onClose: <T>(data?: T) => void;
};

export const GenericDialog = (props: GenericDialogProps) => {
  const { openDialog, dialogTitle, dialogContent, abortLabel, saveLabel, onClose } = props;
  const [open, setOpen] = React.useState(false);

  const componentId = `dialog-${Date.now().toString(36) + Math.random().toString(36).substring(2)}`;

  const handleClose = () => {
    onClose();
    setOpen(false);
  };

  React.useEffect(() => {
    setOpen(openDialog);
  }, [openDialog]);

  return (
    <Dialog
      onClose={handleClose}
      aria-labelledby={`${componentId}-titel`}
      open={open}
      maxWidth='sm'
      sx={{
        '& .MuiDialogContent-root': {
          p: 2,
        },
        '& .MuiDialogActions-root': {
          p: 1,
        },
      }}
    >
      {dialogTitle && (
        <DialogTitle sx={{ m: 0, p: 2 }} id={`${componentId}-titel`}>
          {dialogTitle}
          <IconButton
            aria-label='close'
            onClick={onClose}
            sx={{
              position: 'absolute',
              right: 8,
              top: 8,
              color: (theme) => theme.palette.grey[500],
            }}
          >
            <CloseIcon />
          </IconButton>
        </DialogTitle>
      )}
      <DialogContent dividers>{dialogContent}</DialogContent>
      <DialogActions>
        <Button color='primary' disableElevation onClick={handleClose}>
          {abortLabel}
        </Button>
        <Button autoFocus variant='contained' color='primary' disableElevation onClick={handleClose}>
          {saveLabel}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

GenericDialog.defaultProps = {
  dialogTitle: null,
  abortLabel: 'Avbryt',
  saveLabel: 'Spara',
};
