import { AppBar, Toolbar, IconButton, Typography, Divider, Box, Badge } from '@mui/material';
import { Apps, Star, Bookmarks, ListOutlined } from '@mui/icons-material';
import { Link } from 'react-router-dom';
import { RoutesEnum } from 'src/shared/enums/routes.enum';
import { useContext } from 'react';
import { GameContext } from 'src/shared/state/context';
import { toogleListDrawer } from 'src/shared/state/reducer';
import { HideOnScroll } from 'src/shared/components/organisms/hideOnScroll';
import { LogoImage } from 'src/shared/components/atoms/logoImage';
import { useLiveQuery } from 'dexie-react-hooks';
import { db } from 'src/database';

export const TickListHeader = (props: any) => {
  const { state, dispatch } = useContext(GameContext);

  const allLists = useLiveQuery(() => db.tickList.toArray());

  const showListDrawer = () => {
    dispatch(toogleListDrawer());
  };
  return (
    <>
      <HideOnScroll {...props}>
        <AppBar sx={{ borderBottomWidth: '2px', borderBottomStyle: 'solid', borderBottomColor: 'secondary.main' }}>
          <Toolbar>
            {/* <IconButton
              size='large'
              edge='start'
              color='inherit'
              aria-label='menu'
              sx={{
                mr: 2,
              }}
              onClick={() => showListDrawer()}
            >
              <Menu />
            </IconButton> */}
            <Box
              sx={{ display: 'flex', alignItems: 'center', flexGrow: 1, color: 'white', textDecoration: 'none', mr: 3 }}
              component={Link}
              to={RoutesEnum.Default}
            >
              <Box sx={{ pr: 1, mr: 1, borderRight: '1px solid rgba(255,255,255,0.3)', display: 'flex' }}>
                <LogoImage />
              </Box>
              <Typography variant='h6' component='div' sx={{ flexGrow: 1, fontWeight: 100 }}>
                Kryssa
              </Typography>
            </Box>
            <div>
              <IconButton size='large' color='inherit' component={Link} to={RoutesEnum.ListOverview}>
                <Badge badgeContent={allLists?.length} color='secondary'>
                  <Apps />
                </Badge>
              </IconButton>
              <IconButton size='large' color='inherit' component={Link} to={RoutesEnum.ListFavourites}>
                <Badge badgeContent={allLists?.filter((x) => x.favourite).length} color='secondary'>
                  <Star />
                </Badge>
              </IconButton>
              <IconButton size='large' color='inherit' component={Link} to={RoutesEnum.Tags}>
                <Bookmarks />
              </IconButton>
              {/* <IconButton size='large' color='inherit' component={Link} to={RoutesEnum.ListImport}>
                <PostAdd />
              </IconButton> */}
            </div>
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar id='back-to-top-anchor' />
    </>
  );
};
