import { Box, Container } from '@mui/material';
import CssBaseline from '@mui/material/CssBaseline';
import { ThemeProvider } from '@mui/material/styles';
import { ServiceWorkerWrapper } from 'src/serviceWorkerWrapper';
import { InitDatabase } from 'src/shared/components/organisms/initDatabase';
import { theme } from 'src/shared/components/styles/theme';
import { LayoutPageProperties } from 'src/shared/models/layoutPageProperties';
import { TickListHeader } from '../components/organisms/tickListHeader';
import { ListDrawer } from '../components/organisms/listDrawer';
import { ScrollToTop } from '../components/atoms/scrollToTop';

const ListTemplate = ({ Component, route }: LayoutPageProperties) => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <ServiceWorkerWrapper />
      <TickListHeader />
      <ListDrawer />
      <InitDatabase />
      <Box component='main'>
        <Container sx={{ mt: 2 }}>
          <Component route={route} />
        </Container>
        <ScrollToTop />
      </Box>
    </ThemeProvider>
  );
};

export default ListTemplate;
