import { Box } from '@mui/material';

type RatingIndicatorProps = {
  rating: number;
  index: number;
};

export const RatingIndicator = (props: RatingIndicatorProps) => {
  const { rating, index } = props;
  const colors = ['#ffffff', '#f1f8e9', '#c5e1a5', '#9ccc65', '#7cb342', '#558b2f'];
  return (
    <Box
      sx={{
        width: '1.2rem',
        height: '1.2rem',
        borderRadius: 1,
        mr: 1,
        backgroundColor: colors[rating],
        // eslint-disable-next-line no-nested-ternary
        color: index > 0 ? '#999999' : rating < 3 ? 'black' : 'white',
        fontSize: '12px',
        textAlign: 'center',
        fontWeight: rating > 0 ? 500 : 300,
      }}
    >
      {index > 0 ? index : rating}
    </Box>
  );
};
