import { Tabs, Tab } from '@mui/material';
import { useState } from 'react';

interface TabItem {
  id: number;
  label: string;
}

type TickListTabsProps = {
  tabItems: TabItem[];
  callback: (activeTab: number) => void;
};

export const TickListTabs = (props: TickListTabsProps) => {
  const { callback, tabItems } = props;
  const [activeTab, setActiveTab] = useState(0);

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setActiveTab(newValue);
    callback(newValue);
  };

  return (
    <Tabs
      value={activeTab}
      onChange={handleTabChange}
      aria-label='basic tabs example'
      sx={{
        minHeight: '2rem',
        pl: 2,
        '& .MuiButtonBase-root': { minHeight: '2.5rem', fontSize: '0.75rem', mx: '5px' },
        // '& .Mui-selected': { backgroundColor: 'rgba(48, 95, 120, 0.08)' },
      }}
    >
      {tabItems.map((item: TabItem) => (
        <Tab label={item.label} key={item.id} />
      ))}
    </Tabs>
  );
};
