import { RoutesEnum, RouteTypeEnum } from 'src/shared/enums/routes.enum';
import { Switch, Route } from 'react-router-dom';
import { PageNotfound } from 'src/modules/notfound/pages/notfoundPage';
import { StartPage } from 'src/modules/start/pages/startPage';
import { ListOverviewPage } from 'src/modules/lists/pages/overview';
import { ImportListPage } from 'src/modules/lists/pages/importList';
import { ViewTickListPage } from 'src/modules/lists/pages/viewTickList';
import { AppTemplate, PageTemplate, ListTemplate } from 'src/shared/templates';
import { PageProperties, RouteProperties, LayoutPageProperties } from 'src/shared/models/layoutPageProperties';
import { FavouritesPage } from 'src/modules/lists/pages/listFavourites';
import { TagsPage } from 'src/modules/lists/pages/tagsPage';

const routes: RouteProperties[] = [
  {
    exact: true,
    path: RoutesEnum.Default,
    template: ListTemplate,
    component: ListOverviewPage,
  },
  // {
  //   exact: true,
  //   path: RoutesEnum.Start,
  //   template: AppTemplate,
  //   component: StartPage,
  // },
  {
    exact: true,
    path: RoutesEnum.ListFavourites,
    template: ListTemplate,
    component: FavouritesPage,
  },
  {
    exact: true,
    path: RoutesEnum.ListOverview,
    template: ListTemplate,
    component: ListOverviewPage,
  },
  {
    exact: true,
    path: RoutesEnum.ListImport,
    template: ListTemplate,
    component: ImportListPage,
  },
  {
    exact: true,
    path: RoutesEnum.ListDetail,
    template: ListTemplate,
    component: ViewTickListPage,
  },
  {
    exact: true,
    path: RoutesEnum.Tags,
    template: ListTemplate,
    component: TagsPage,
  },
];

export const PageRoutes = () => {
  return (
    <Switch>
      {routes.map((routeItem: RouteProperties) => {
        return (
          <Route
            exact={routeItem.exact}
            path={routeItem.path}
            key={routeItem.path}
            render={(route) => <routeItem.template Component={routeItem.component} route={route} />}
          />
        );
      })}
      {/* 
      <Route path={RoutesEnum.Start} exact component={StartPage} />
      <Route path={RoutesEnum.Document} exact component={DocumentPage} />
      */}
      <Route component={PageNotfound} />
    </Switch>
  );
};
