import { Star } from '@mui/icons-material';
import { Box, Typography, Grow } from '@mui/material';
import { TickList } from 'src/database/models/tickList';
import { TickListActionMenu } from './tickListActionMenu';

type TickListNameProps = {
  tickList: TickList;
};

export const TickListName = (props: TickListNameProps) => {
  const { tickList } = props;
  return (
    <Box
      sx={{
        pt: 4,
        px: 2,
        pb: 2,
        mt: -3,
        display: 'flex',
        justifyContent: 'space-between',
      }}
    >
      {tickList && tickList.listName && (
        <>
          <Typography variant='h5' sx={{ display: 'flex' }} gutterBottom>
            <Box sx={{ mr: 1.5 }}>
              <Box
                component='span'
                sx={{
                  backgroundColor: 'secondary.dark',
                  py: 0.5,
                  px: 1,
                  color: '#fff',
                  borderRadius: '5px',
                }}
              >
                {tickList?.tickCount}
              </Box>
            </Box>
            <Box sx={{ lineHeight: 1 }}>
              <Typography
                variant='caption'
                component='div'
                sx={{
                  lineHeight: '0.7rem',
                  color: 'text.secondary',
                }}
              >
                {tickList.listType}
              </Typography>
              {tickList?.listName} <small>{tickList?.year}</small>
              <Grow in={tickList.favourite}>
                <Star fontSize='small' sx={{ ml: 0.5 }} />
              </Grow>
            </Box>
          </Typography>
          <TickListActionMenu tickList={tickList} />
        </>
      )}
    </Box>
  );
};
