import { ContentCopy, ExpandMore, InfoOutlined, FavoriteOutlined } from '@mui/icons-material';
import { Paper, Typography, Button, Link, Accordion, AccordionDetails, AccordionSummary, Divider } from '@mui/material';
import { useState } from 'react';

export const CopyInstructions = () => {
  const [expanded, setExpanded] = useState<string | false>(false);

  const handleChange = (panel: string) => (event: React.SyntheticEvent, isExpanded: boolean) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <Accordion
      expanded={expanded === 'panel1'}
      onChange={handleChange('panel1')}
      sx={{
        my: 2,
        borderRadius: 1,
        '&:before': {
          display: 'none',
        },
        '& ul': {
          paddingLeft: '1.3rem',
          '& li': {
            mb: 1,
          },
        },
      }}
    >
      <AccordionSummary expandIcon={<ExpandMore />} aria-controls='panel1bh-content' id='panel1bh-header'>
        <InfoOutlined fontSize='small' sx={{ color: 'text.disabled', mr: 1, mt: '1px' }} />
        <Typography sx={{ fontWeight: 500 }}>Hur importerar man listor?</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Typography variant='body2'>Flj nedanstende instruktionerna fr att kopiera dina listor frn Artportalen (en i taget).</Typography>
        <Typography variant='body2' component='div'>
          <ul>
            <li>
              Surfa till <Link href='https://www.artportalen.se'>Artportalen</Link>
            </li>
            <li>
              Skapa ett nytt bokmrke (namnge det <em>Kopiera lista</em> eller liknande).
            </li>
            <li>Tryck p knappen nedan.</li>
          </ul>
          <Button
            size='medium'
            color='primary'
            variant='outlined'
            sx={{
              display: 'flex',
              ml: '1.3rem',
            }}
            startIcon={<ContentCopy />}
          >
            Skapa bokmrke
          </Button>

          <ul>
            <li>Redigera bokmrke du skapade och klistra in koden du nyss kopierade i fltet fr webbadress. Spara bokmrket med koden.</li>
            <li>ppna en lista i Artportalen (logga in s fr du med skyddade fynd).</li>
            <li>Klicka p bokmrket du nyss skapade.</li>
            <li>Kopiera resultatet som visas p skrmen</li>
            <li>
              Klistra in i rutan ovan och klicka p knappen <em>Importera</em>
            </li>
          </ul>
        </Typography>
        <Divider sx={{ mb: 2 }} />
        <Typography variant='caption' component='div'>
          <FavoriteOutlined sx={{ fontSize: '0.7rem', mr: 1 }} />
          Gillar du den hr appen? Swisha eller skicka en annan uppmuntran till kreatren...
        </Typography>
      </AccordionDetails>
    </Accordion>
  );
};
