import { useLiveQuery } from 'dexie-react-hooks';
import { useEffect, useState } from 'react';
import { db } from 'src/database';
import { GenericDialog } from 'src/shared/components/organisms/genericDialog';
import { WantedRating } from './wantedRating';

type EditTickProps = {
  taxonId: number;
  tickListId: number;
  callback: () => void;
};

export const EditTick = (props: EditTickProps) => {
  const { tickListId, taxonId, callback } = props;

  const [selectedTaxonId, setSelectedTaxonId] = useState<number | null>(taxonId);
  const [showEditTickDialog, setShowEditTickDialog] = useState<boolean>(false);

  const selectedTaxaList = useLiveQuery(() => db.tickList.where('id').equals(tickListId).toArray());

  const openDialog = (id: number) => {
    setSelectedTaxonId(id);
    setShowEditTickDialog(true);
  };

  const closeDialog = () => {
    setSelectedTaxonId(null);
    setShowEditTickDialog(false);
    callback();
  };

  const [tickDate, setTickDate] = useState<Date>(new Date());

  useEffect(() => {
    if (selectedTaxonId) {
      openDialog(selectedTaxonId);
    }
  }, [selectedTaxonId]);

  useEffect(() => {
    setSelectedTaxonId(taxonId);
  }, [taxonId]);

  useEffect(() => {
    if (selectedTaxaList !== undefined) {
      console.log('selectedTaxaList', selectedTaxaList);
    }
  }, [selectedTaxaList]);

  return (
    <>
      <GenericDialog
        openDialog={showEditTickDialog}
        dialogContent={
          <>
            <WantedRating taxonId={taxonId} tickListId={tickListId} rating={0} callback={closeDialog} />
          </>
        }
        onClose={closeDialog}
      />
    </>
  );
};
