import { Taxon } from 'src/database/models/taxon';

type SpeciesNameFormattedProps = {
  name: string;
  rank: number;
};
export const SpeciesNameFormatted = (props: SpeciesNameFormattedProps) => {
  const { name, rank } = props;

  return (
    <span
      className={`speciesName 
        ${rank < 3000 ? 'red' : ''} 
        ${rank < 20000 ? 'bold' : ''}
      `}
    >
      {name}
    </span>
  );
};
