import { Paper } from '@mui/material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useContext } from 'react';
import { db } from 'src/database';
import { GameContext } from 'src/shared/state/context';
import { TagActiveView } from '../components/tagActiveView';
import { TagEdit } from '../components/tagEdit';

export const TagsPage = () => {
  const { state, dispatch } = useContext(GameContext);

  const allLists = useLiveQuery(() => db.tickList.toArray());

  return (
    <>
      {state.activeTagId && <TagActiveView />}
      {!state.activeTagId && <TagEdit />}
    </>
  );
};
