import * as React from 'react';
import { Dialog, DialogTitle, DialogContent, DialogActions, Button } from '@mui/material';
import { toast } from 'react-toastify';

export type DeleteConfirmationDialogProps = {
  title: string;
  modaltext?: string;
  value: string | null;
  open: boolean;
  onClose: (value?: any) => void;
};

export const DeleteConfirmationDialog = (props: DeleteConfirmationDialogProps) => {
  const { title, modaltext, onClose, value: valueProp, open, ...other } = props;

  const dialogId = `confirmation-dialog-title-${Date.now().toString(36) + Math.random().toString(36).substring(2)}`;

  const handleCancel = () => {
    onClose();
  };

  const handleOk = () => {
    onClose({ id: props.value });
  };

  return (
    <Dialog onBackdropClick={() => false} disableEscapeKeyDown maxWidth='xs' aria-labelledby={dialogId} open={open} {...other}>
      <DialogTitle id={dialogId}>{title}</DialogTitle>
      {modaltext && <DialogContent>{modaltext}</DialogContent>}
      <DialogActions>
        <Button autoFocus onClick={handleCancel} color='primary'>
          Avbryt
        </Button>
        <Button onClick={handleOk} color='primary' variant='contained'>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );
};
