import { ExpandMore, ListAlt, Star, InfoOutlined } from '@mui/icons-material';
import {
  Paper,
  Box,
  Accordion,
  AccordionSummary,
  Typography,
  AccordionDetails,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from '@mui/material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useContext, useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { db } from 'src/database';
import { TickList } from 'src/database/models/tickList';
import { ListTypeEnum } from 'src/shared/enums/listTypeEnum';
import { GameContext } from 'src/shared/state/context';
import { setExpandedListType } from 'src/shared/state/reducer';

type TickListOverviewProps = {
  persistChanges?: boolean;
  excludeTickListId?: number;
  callback?: (list: TickList) => void;
};

export const TickListOverview = (props: TickListOverviewProps) => {
  const { callback, persistChanges, excludeTickListId } = props;
  const { state, dispatch } = useContext(GameContext);
  const [expanded, setExpanded] = useState<boolean>(true);
  const [activeListType, setActiveListType] = useState<ListTypeEnum>(state.expandedTickListType);

  const handleChange = (listType: ListTypeEnum) => (event: React.SyntheticEvent, isExpanded: boolean) => {
    setExpanded(isExpanded);
    if (persistChanges) {
      dispatch(setExpandedListType(listType));
    } else {
      setActiveListType(listType);
    }
  };

  const allLists = useLiveQuery(() => db.tickList.toArray());

  const listTypes = Object.values(ListTypeEnum);

  const setListName = (listType: ListTypeEnum) => {
    switch (listType) {
      case ListTypeEnum.Landskap:
        return 'Landskap';
      case ListTypeEnum.Lokal:
        return 'Lokal';
      case ListTypeEnum.Kommun:
        return 'Kommun';
      case ListTypeEnum.Custom:
        return 'vriga';
      default:
        return '';
    }
  };

  const [tickLists, setTickLists] = useState<TickList[]>([]);

  useEffect(() => {
    if (allLists !== undefined) {
      if (excludeTickListId) {
        setTickLists(allLists.filter((x) => x.id !== excludeTickListId));
      } else {
        setTickLists(allLists);
      }
    }
  }, [allLists]);

  useEffect(() => {
    setActiveListType(state.expandedTickListType);
  }, [state.expandedTickListType]);

  return (
    <Box
      sx={{
        '& .MuiListItemIcon-root': {
          minWidth: '2rem',
        },
        '& .MuiListItemSecondaryAction-root': {
          fontWeight: 600,
        },
        '& .MuiAccordionSummary-content': {
          m: 0,
        },
        '& a': {
          color: 'text.primary',
        },
      }}
    >
      {!allLists ||
        (!tickLists.length && (
          <Paper>
            <Typography variant='h6' sx={{ py: 1, px: 2, display: 'flex', alignItems: 'center' }}>
              <InfoOutlined fontSize='small' sx={{ color: 'text.disabled', mr: 1 }} />
              {excludeTickListId ? 'Listor att jmfra saknas' : 'Listor saknas'}
            </Typography>
          </Paper>
        ))}
      {allLists &&
        tickLists &&
        listTypes.map((type) => {
          const currentTickList = tickLists.filter((item: TickList) => item.listType === type);
          if (!currentTickList.length) return null;
          return (
            <Accordion expanded={expanded && type === activeListType} onChange={handleChange(type)} key={type}>
              <AccordionSummary
                expandIcon={<ExpandMore />}
                sx={{
                  '& .MuiAccordionSummary-content': {
                    alignItems: 'center',
                  },
                }}
              >
                <Typography variant='h6' component='span' sx={{ fontWeight: 600 }}>
                  {setListName(type)}
                </Typography>
                <Typography component='span' sx={{ ml: 1 }}>
                  ({currentTickList.length})
                </Typography>
              </AccordionSummary>
              <AccordionDetails
                sx={{
                  p: 0,
                }}
              >
                <Divider />
                <List component='div' disablePadding>
                  {currentTickList
                    .sort((a, b) => {
                      return a.listName > b.listName ? 1 : -1;
                    })
                    .map((item: TickList, index: number) => {
                      return (
                        <ListItem
                          key={item.id}
                          secondaryAction={
                            <Typography
                              component='span'
                              variant='body2'
                              sx={{
                                borderRadius: 1,
                                backgroundColor: 'secondary.main',
                                color: 'white',
                                px: 0.8,
                                py: 0.3,
                                fontWeight: 500,
                              }}
                            >
                              {item.tickCount}
                            </Typography>
                          }
                          {...(callback ? { onClick: () => callback(item) } : { component: Link, to: `/list/${item.id}` })}
                          divider={index !== currentTickList.length - 1}
                          dense
                        >
                          <ListItemIcon>
                            <ListAlt />
                          </ListItemIcon>
                          <ListItemText
                            primary={
                              <>
                                {item.listName} {item.year}
                                {item.favourite ? (
                                  <Star
                                    fontSize='small'
                                    sx={{
                                      ml: '5px',
                                      mt: '2px',
                                      fontSize: '14px',
                                      verticalAlign: 'baseline',
                                    }}
                                  />
                                ) : null}
                              </>
                            }
                          />
                        </ListItem>
                      );
                    })}
                </List>
              </AccordionDetails>
            </Accordion>
          );
        })}
    </Box>
  );
};

TickListOverview.defaultProps = {
  persistChanges: true,
  excludeTickListId: null,
  callback: undefined,
};
