import { ListTypeEnum } from 'src/shared/enums/listTypeEnum';

export interface GameState {
  players: Player[];
  gameName: string;
  listViewType: boolean;
  listDrawerVisible: boolean;
  sortListAsc: boolean;
  sortListBy: SortTickListEnum;
  expandedTickListType: ListTypeEnum;
  activeTagId: number | null;
  focusQuantity: boolean;
  focusSpeciesName: boolean;
  winner: Player | null;
  gameStatus: Status;
}

export enum Status {
  NotStarted = 'Not Started',
  InProgress = 'In Progress',
  Finished = 'Finished',
}

export enum SortTickListEnum {
  SpeciesName = 'speciesname',
  Date = 'date',
}

export interface Player {
  name: string;
  id: number;
  status: Status;
  value?: number;
}

export const initialGameState: GameState = {
  players: [],
  gameName: 'Game1',
  listViewType: true,
  listDrawerVisible: false,
  sortListAsc: true,
  sortListBy: SortTickListEnum.Date,
  expandedTickListType: ListTypeEnum.Lokal,
  activeTagId: null,
  focusQuantity: true,
  focusSpeciesName: false,
  winner: null,
  gameStatus: Status.NotStarted,
};
