import { Box, List, ListItem, ListItemButton, ListItemText, Typography } from '@mui/material';
import { Dexie } from 'dexie';
import { useLiveQuery } from 'dexie-react-hooks';
import { useEffect, useState } from 'react';
import { db } from 'src/database';
import { Taxon } from 'src/database/models/taxon';
import { TickList } from 'src/database/models/tickList';
import { WantedTickList } from 'src/database/models/wantedTickList';
import { RatingIndicator } from './ratingIndicator';
import { SpeciesNameFormatted } from './seciesNameFormatted';

type TagListWantedProps = {
  tagId: number;
};

interface RatedTaxon extends Taxon {
  rating: number;
}

interface Wanted {
  wanted: WantedTickList[];
  tickList: TickList[];
  ratedTaxa: RatedTaxon[];
}

export const TagListWanted = (props: TagListWantedProps) => {
  const { tagId } = props;

  const [tickLists, setTickLists] = useState<Wanted[]>([]);
  const [listForRender, setListForRender] = useState<Wanted[]>([]);

  const allTaxa = useLiveQuery(() => db.taxon.toArray());

  const { all } = Dexie.Promise;

  const joinLists = (listCollection: any) => {
    // Start by getting all bands as an array of band objects
    return listCollection.toArray((bands: any) => {
      // Query related properties:
      const genresPromises = bands.map((band: any) => {
        return db.wantedTickList.where('id').anyOf(band.tickListId).toArray();
      });
      const albumsPromises = bands.map((band: any) => {
        return db.tickList.where('id').anyOf(band.tickListId).toArray();
      });

      // Await genres and albums queries:
      return all([all(genresPromises), all(albumsPromises)]).then(function (genresAndAlbums) {
        // Now we have all foreign keys resolved and
        // we can put the results onto the bands array
        // before returning it:
        const result: Wanted[] = [];
        bands.forEach(function (band: any, i: number) {
          result.push({
            wanted: genresAndAlbums[0][i],
            tickList: genresAndAlbums[1][i],
          } as Wanted);
        });
        return result;
      });
    });
  };

  useEffect(() => {
    console.log('kom?!');

    joinLists(db.tagLists.where('tagId').equals(tagId))
      .then((wantedLists: Wanted[]) => {
        setTickLists(wantedLists);
        // wantedLists.forEach((item: Wanted) => {
        //   console.log(`Listanme: ${item.tickList[0].listName}`);
        // });
      })
      .catch((error: any) => {
        console.log(`Oops: ${error}`);
      });
  }, []);

  useEffect(() => {
    if (allTaxa && tickLists) {
      const tempResult: Wanted[] = [];
      tickLists.forEach((item: Wanted) => {
        console.log('item.wanted[0]', item.wanted[0]);
        tempResult.push({
          wanted: item.wanted,
          tickList: item.tickList,
          ratedTaxa:
            (item.wanted[0] &&
              item.wanted[0].ticks.map((x) => {
                return { ...allTaxa.filter((z) => z.taxonId === x.taxonId)[0], rating: x.rating } as RatedTaxon;
              })) ??
            [],
        } as unknown as Wanted);
      });
      console.log(tempResult);
      setListForRender(tempResult);
    }
  }, [allTaxa, tickLists]);

  return (
    <Box
      sx={{
        borderTopWidth: 1,
        borderTopStyle: 'solid',
        borderTopColor: 'divider',
      }}
    >
      {listForRender &&
        listForRender.map((item: Wanted) => {
          return (
            <List dense sx={{ p: 0 }}>
              <ListItem
                divider
                sx={{ borderBottomWidth: '3px' }}
                secondaryAction={
                  <Typography
                    component='span'
                    variant='body2'
                    sx={{
                      borderRadius: 1,
                      backgroundColor: 'secondary.main',
                      color: 'white',
                      px: 0.8,
                      py: 0.3,
                      fontWeight: 500,
                    }}
                  >
                    {item.tickList[0]?.tickCount}
                  </Typography>
                }
              >
                <Typography variant='h6' key={item.tickList[0]?.id}>
                  {item.tickList[0]?.listName}
                </Typography>
              </ListItem>
              {!item.ratedTaxa.length && (
                <ListItem>
                  <Typography variant='body2'>
                    <em>Frvntade arter har inte valts</em>
                  </Typography>
                </ListItem>
              )}
              {item.ratedTaxa
                .sort((a, b) => {
                  return a.rating > b.rating ? -1 : 0;
                })
                .map((taxa: RatedTaxon) => (
                  <ListItemButton key={taxa.taxonId} dense divider className={taxa.rating ? 'rated' : ''}>
                    <RatingIndicator rating={taxa.rating} index={0} />
                    <ListItemText primary={<SpeciesNameFormatted name={taxa.name} rank={taxa.rank} />} />
                  </ListItemButton>
                ))}
            </List>
          );
        })}
    </Box>
  );
};
