import * as React from 'react';
import { CallSplit, FormatListBulleted } from '@mui/icons-material';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

type ToggleUniqueButtonProps = {
  callback: (state: boolean) => void;
};

export const ToggleUniqueButton = (props: ToggleUniqueButtonProps) => {
  const { callback } = props;
  const [isUnique, setIsUnique] = React.useState<boolean>(false);

  const handleChange = (event: React.MouseEvent<HTMLElement>, showUnique: boolean) => {
    setIsUnique(showUnique);
    callback(showUnique);
  };

  return (
    <ToggleButtonGroup
      size='small'
      color='primary'
      value={isUnique}
      exclusive
      onChange={handleChange}
      sx={{
        m: 1,
        backgroundColor: 'background.paper',
        '& button': {
          padding: '3px 10px',
        },
      }}
    >
      <ToggleButton value={false}>
        <FormatListBulleted fontSize='small' sx={{ width: '16px', height: '16px', mr: 1 }} />
        Alla
      </ToggleButton>
      <ToggleButton value>
        <CallSplit fontSize='small' sx={{ width: '16px', height: '16px', mr: 1 }} />
        Unika
      </ToggleButton>
    </ToggleButtonGroup>
  );
};
