import Dexie, { Table } from 'dexie';
import { Taxon } from 'src/database/models/taxon';
import { TickList } from 'src/database/models/tickList';
import { Tag } from 'src/database/models/tag';
import { TagList } from 'src/database/models/tagList';
import { WantedTickList } from 'src/database/models/wantedTickList';

class AppDB extends Dexie {
  taxon!: Table<Taxon>;

  tickList!: Table<TickList>;

  wantedTickList!: Table<WantedTickList>;

  tags!: Table<Tag>;

  tagLists!: Table<TagList>;

  constructor() {
    super('appDB');
    this.version(3).stores({
      taxon: `++id, taxonId, includedByTaxonId, name, scientificName, rubin, sortOrder, isCountable, rank`,
      tickList: `++id, listName, listType, ticks, tickCount, year, updated, created, imported, favourite, apUrl`,
      wantedTickList: `++id, &tickListId, wantedTicks`,
      tags: '++id, name, color',
      tagLists: '++id, tagId, tickListId',
    });
  }
}

export const db = new AppDB();
