import { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { Button } from '@mui/material';
import { PostAdd, AddCircle } from '@mui/icons-material';
import { TickListOverview } from 'src/modules/lists/components/tickListOverview';
import { RoutesEnum } from 'src/shared/enums/routes.enum';
import { EditTickListDialog } from 'src/modules/lists/components/editTickListDialog';

export const ListOverviewPage = () => {
  const [openAddDialog, setOpenAddDialog] = useState<boolean>(false);
  const createList = () => {
    setOpenAddDialog(true);
  };

  const handleCloseEditTickDialog = () => {
    setOpenAddDialog(false);
  };

  return (
    <>
      {openAddDialog && <EditTickListDialog tickList={null} open={openAddDialog} onClose={handleCloseEditTickDialog} value={null} />}
      <TickListOverview />
      <Button
        size='large'
        color='primary'
        variant='contained'
        sx={{
          mt: 2,
          width: '100%',
        }}
        component={Link}
        to={RoutesEnum.ListImport}
        startIcon={<PostAdd />}
      >
        Importera lista
      </Button>
      <Button
        size='large'
        color='primary'
        variant='contained'
        sx={{
          mt: 2,
          width: '100%',
        }}
        onClick={createList}
        startIcon={<AddCircle />}
      >
        Skapa lista
      </Button>
    </>
  );
};
