import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useLiveQuery } from 'dexie-react-hooks';
import { TickList } from 'src/database/models/tickList';
import { db } from 'src/database';
import { ListAlt, Star } from '@mui/icons-material';
import { Paper, Typography, Divider, List, ListItem, ListItemIcon, ListItemText, ListSubheader, alpha } from '@mui/material';

export const FavouritesPage = () => {
  const [favouriteTickLists, setFavouriteTickLists] = useState<TickList[]>([]);
  const allLists = useLiveQuery(() => db.tickList.toArray());

  useEffect(() => {
    if (allLists !== undefined) {
      setFavouriteTickLists(
        allLists
          .filter((item) => item.favourite)
          .sort((a, b) => {
            return a.listName > b.listName ? 1 : -1;
          }),
      );
    }
  }, [allLists]);

  return (
    <>
      {favouriteTickLists && (
        <Paper
          sx={{
            mb: 2,
            '& .MuiListItemIcon-root': {
              minWidth: '2rem',
            },
            '& a': {
              color: 'text.primary',
              '&:hover': {
                backgroundColor: (theme) => alpha(theme.palette.primary.light, 0.1),
              },
            },
          }}
        >
          <Typography variant='h6' sx={{ py: 1, px: 2, display: 'flex', alignItems: 'center' }}>
            <Star fontSize='small' sx={{ color: 'text.disabled', mr: 1 }} />
            Favoriter ({favouriteTickLists.length})
          </Typography>
          <Divider />
          <List component='div' disablePadding>
            {favouriteTickLists.map((item: TickList, index: number) => {
              return (
                <ListItem
                  key={item.id}
                  secondaryAction={
                    <Typography
                      component='span'
                      variant='body2'
                      sx={{
                        borderRadius: 1,
                        backgroundColor: 'secondary.main',
                        color: 'white',
                        px: 0.8,
                        py: 0.3,
                        fontWeight: 500,
                      }}
                    >
                      {item.tickCount}
                    </Typography>
                  }
                  component={Link}
                  to={`/list/${item.id}`}
                  divider
                  dense
                >
                  <ListItemIcon>
                    <ListAlt />
                  </ListItemIcon>
                  <ListItemText
                    primary={
                      <>
                        {item.listName} {item.year}
                        {/* {item.favourite ? <Star fontSize='small' sx={{ ml: 1, fontSize: '14px' }} /> : null} */}
                      </>
                    }
                  />
                </ListItem>
              );
            })}
          </List>
        </Paper>
      )}
    </>
  );
};
