import { List, ListItemButton, ListItemIcon, ListItemText, Paper, Badge, ListItemSecondaryAction, CircularProgress } from '@mui/material';
import { AddCircleOutline } from '@mui/icons-material';
import { useCallback, useEffect, useState } from 'react';
import { Taxon } from 'src/database/models/taxon';
import { db } from 'src/database';
import { useLiveQuery } from 'dexie-react-hooks';
import { SpeciesNameFormatted } from './seciesNameFormatted';
import { EditTick } from './editTick';
import { RatingIndicator } from './ratingIndicator';

type WantedSpeciesListProps = {
  tickListId: number;
  wantedSpeciesList: Taxon[];
};

interface RatedTaxon extends Taxon {
  rating: number;
}

export const WantedSpeciesList = (props: WantedSpeciesListProps) => {
  const { wantedSpeciesList, tickListId } = props;
  const [expectedSpecies, setExpectedSpecies] = useState<RatedTaxon[]>([]);

  const expextedTickList = useLiveQuery(() => db.wantedTickList.where('tickListId').equals(tickListId).toArray());

  useEffect(() => {
    const expectedTaxons: RatedTaxon[] = [];
    if (expextedTickList) {
      if (expextedTickList !== undefined && expextedTickList[0]) {
        wantedSpeciesList.forEach((item) => {
          const exists = expextedTickList[0].ticks.filter((x) => x.taxonId === item.taxonId);
          const newRatedTaxon = { ...item, rating: exists.length ? exists[0].rating : 0 };
          expectedTaxons.push(newRatedTaxon);
        });
        setExpectedSpecies(
          expectedTaxons.sort((a, b) => {
            return a.rating > b.rating ? -1 : 0;
          }),
        );
      } else {
        wantedSpeciesList.forEach((item) => {
          expectedTaxons.push({ ...item, rating: 0 });
        });
        setExpectedSpecies(expectedTaxons);
      }
    }
  }, [expextedTickList]);

  const [taxonId, setTaxonId] = useState<number | null>(0);

  const handeCloseTickEdit = useCallback(() => {
    setTaxonId(null);
  }, []);

  if (!expextedTickList) return null;

  let nonRatingIndex = 0;

  return (
    <Paper sx={{ mx: -2 }} square elevation={0}>
      {expextedTickList && (
        <>
          {taxonId !== null && <EditTick tickListId={tickListId} taxonId={taxonId} callback={() => handeCloseTickEdit()} />}
          <List
            disablePadding
            sx={{
              '& li:nth-of-type(even)': { backgroundColor: 'rgba(0,0,0,0.025)' },
              '& .MuiListItemIcon-root': {
                minWidth: '1rem',
              },
              '& .rated': {
                '& .speciesName': {
                  fontWeight: 500,
                },
              },
            }}
          >
            {expectedSpecies &&
              expectedSpecies.map((item: RatedTaxon, index: number) => {
                if (item.rating === 0) nonRatingIndex += 1;
                return (
                  <ListItemButton key={item.taxonId} dense divider onClick={() => setTaxonId(item.taxonId)} className={item.rating ? 'rated' : ''}>
                    <RatingIndicator rating={item.rating} index={nonRatingIndex} />
                    <ListItemText primary={<SpeciesNameFormatted name={item.name} rank={item.rank} />} />
                    <ListItemIcon>
                      <AddCircleOutline />
                    </ListItemIcon>
                  </ListItemButton>
                  // <WantedListItem key={item.taxonId} speciesName={<SpeciesNameFormatted name={item.name} rank={item.rank} />} />
                );
              })}
          </List>
        </>
      )}
    </Paper>
  );
};
