import { Box, Paper, Stack, Chip, Button, Divider, Collapse, Typography } from '@mui/material';
import { Delete, Done, CompareArrows } from '@mui/icons-material';
import { useCallback, useState } from 'react';
import { TickList } from 'src/database/models/tickList';
import { TransitionGroup } from 'react-transition-group';
import { TickListOverview } from './tickListOverview';
import { CompareGrid } from './compareGrid';

type CompareTickListProps = {
  currentTickList: TickList;
};

export const CompareTickLists = (props: CompareTickListProps) => {
  const { currentTickList } = props;
  const [activeComparison, setActiveComparison] = useState<boolean>(false);
  const [selectedTickLists, setSelectedTickLists] = useState<TickList[]>([]);
  const onListSelected = useCallback((list: TickList) => {
    const exists = selectedTickLists.some((compareList: TickList) => {
      return compareList.id === list.id;
    });

    if (!exists && currentTickList.id !== list.id) {
      setSelectedTickLists((prevArray) => [...prevArray, list]);
    }
  }, []);

  const toggleCompareMode = () => {
    setActiveComparison(!activeComparison);
  };

  const handleDelete = (listId: number) => {
    setSelectedTickLists(selectedTickLists.filter((item: TickList) => item.id !== listId));
  };

  return (
    <Box sx={{ p: 2, mx: -2 }}>
      {!activeComparison && (
        <Paper variant='outlined' sx={{ p: 2, mb: 2 }}>
          <Stack spacing={1}>
            <Chip
              label={currentTickList.listName}
              onDelete={handleDelete}
              deleteIcon={<Done />}
              variant='outlined'
              color='default'
              sx={{
                borderRadius: 1,
                justifyContent: 'space-between',
              }}
            />
            <TransitionGroup>
              {selectedTickLists &&
                selectedTickLists.map((list: TickList) => {
                  return (
                    <Collapse key={list.id}>
                      <Chip
                        label={`${list.listName} ${list.year ? list.year : ''}`}
                        onDelete={() => handleDelete(list.id)}
                        deleteIcon={<Delete />}
                        variant='outlined'
                        color='success'
                        sx={{ borderRadius: 1, justifyContent: 'space-between', width: '100%', mb: 1 }}
                      />
                    </Collapse>
                  );
                })}
            </TransitionGroup>
          </Stack>
          {selectedTickLists && selectedTickLists.length > 0 && (
            <Button
              size='large'
              color='primary'
              variant='contained'
              sx={{
                mt: 2,
                width: '100%',
              }}
              onClick={toggleCompareMode}
              startIcon={<CompareArrows />}
            >
              Jmfr valda listor
            </Button>
          )}
        </Paper>
      )}
      {activeComparison && (
        <>
          <CompareGrid currentTickList={currentTickList} tickListsToCompare={selectedTickLists} />
        </>
      )}
      {!activeComparison && (
        <>
          <Divider sx={{ mb: 2 }}>Vlj listor</Divider>
          <TickListOverview persistChanges={false} callback={onListSelected} excludeTickListId={currentTickList.id} />
        </>
      )}
    </Box>
  );
};
