import { Box, Chip, Typography, Paper, Badge } from '@mui/material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useContext, useEffect, useState, useCallback } from 'react';
import { db } from 'src/database';
import { Tag } from 'src/database/models/tag';
import { GameContext } from 'src/shared/state/context';
import { setActiveTagId } from 'src/shared/state/reducer';
import { TagListOverview } from './tagListOverview';
import { TagListWanted } from './tagListWanted';
import { TagsActionMenu } from './tagsActionMenu';
import { TickListTabPanel } from './tickListTabPanel';
import { TickListTabs } from './tickListTabs';

export const TagActiveView = () => {
  const { state, dispatch } = useContext(GameContext);

  const [activeTab, setActiveTab] = useState(0);

  const handleTabChange = useCallback((newValue: number) => {
    setActiveTab(newValue);
  }, []);

  const [activeTag, setActiveTag] = useState<Tag | null>(null);
  const [tags, setTags] = useState<Tag[]>([]);

  const allTags = useLiveQuery(() => db.tags.toArray());
  useEffect(() => {
    if (allTags !== undefined) {
      setTags(allTags);
      const findTag = allTags.filter((x) => x.id === state.activeTagId);
      if (findTag.length) {
        setActiveTag(findTag[0]);
      }
    }
  }, [allTags]);

  const resetActiveTag = () => {
    dispatch(setActiveTagId(null));
  };

  return (
    <Paper
      elevation={0}
      square
      sx={{
        mx: -2,
        // backgroundColor: activeTag?.color,
        // background: `linear-gradient(0deg, #ffffff 0%, ${activeTag?.color} 100%)`,
      }}
    >
      <Box
        sx={{
          pt: 4,
          px: 2,
          pb: 2,
          mt: -3,
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        <Chip
          label={
            <Typography variant='h5' sx={{ display: 'flex', alignItems: 'center' }}>
              <Box
                sx={{
                  width: '1.2rem',
                  height: '1.2rem',
                  borderRadius: 1,
                  mr: 1,
                  backgroundColor: activeTag?.color,
                }}
              />
              {activeTag?.name}
            </Typography>
          }
          sx={{
            height: '2.3rem',
            borderRadius: 1,
          }}
          variant='outlined'
          onDelete={resetActiveTag}
        />
        {activeTag && <TagsActionMenu tagId={activeTag.id} />}
      </Box>
      <Box
        sx={{
          borderBottom: 1,
          borderColor: 'divider',
        }}
      >
        <TickListTabs
          tabItems={[
            {
              id: 0,
              label: 'Listor',
            },
            {
              id: 1,
              label: 'Frvntade',
            },
          ]}
          callback={handleTabChange}
        />
        <TickListTabPanel value={activeTab} index={0}>
          {activeTab === 0 && activeTag && <TagListOverview tagId={activeTag.id} />}
        </TickListTabPanel>
        <TickListTabPanel value={activeTab} index={1}>
          {activeTab === 1 && activeTag && <TagListWanted tagId={activeTag.id} />}
        </TickListTabPanel>
      </Box>
    </Paper>
  );
};
