import { useParams } from 'react-router';
import { useCallback, useContext, useEffect, useState } from 'react';
import { db } from 'src/database';
import { TickList } from 'src/database/models/tickList';
import { Box, Paper } from '@mui/material';
import { Taxon } from 'src/database/models/taxon';
import { useLiveQuery } from 'dexie-react-hooks';
import { TaxonDisplay } from 'src/shared/models/taxonDisplay';
import { SortTickListEnum } from 'src/shared/state/state';
import { GameContext } from 'src/shared/state/context';
import { SortableTickList } from 'src/modules/lists/components/sortableTickList';
import { TickListTabs } from 'src/modules/lists/components/tickListTabs';
import { TickListName } from 'src/modules/lists/components/tickListName';
import { CompareTickLists } from 'src/modules/lists/components/compareTickLists';
import { WantedSpeciesList } from 'src/modules/lists/components/wantedSpecies';
import { TickListTabPanel } from '../components/tickListTabPanel';

export interface IParams {
  id: string;
}

export const ViewTickListPage = () => {
  const { id } = useParams<IParams>();
  const [activeTab, setActiveTab] = useState(0);

  const handleTabChange = useCallback((newValue: number) => {
    setActiveTab(newValue);
  }, []);

  const { state } = useContext(GameContext);
  const sortByDate = state.sortListBy === SortTickListEnum.Date;

  const [wanted, setWanted] = useState<boolean>(false);
  const [taxaList, setTaxaList] = useState<Taxon[]>([]);
  const [wantedTaxaList, setWantedTaxaList] = useState<Taxon[]>([]);
  const [tickList, setTickList] = useState<TickList | undefined>();
  const [displayTaxaList, setDisplayTaxaList] = useState<TaxonDisplay[] | undefined>([]);

  const allTaxa = useLiveQuery(() => db.taxon.toArray());
  const tickListTableData = useLiveQuery(() => db.tickList.get(+id));

  useEffect(() => {
    if (allTaxa !== undefined && tickListTableData !== undefined) {
      setTaxaList(allTaxa.filter((x) => x.isCountable));
      setTickList(tickListTableData);
      if (activeTab === 1) {
        const wantedTicks = allTaxa
          .filter((x) => x.isCountable && !tickListTableData.ticks.some((existingTaxa) => x.taxonId === existingTaxa.taxonId))
          .sort((a, b) => {
            return b.rank - a.rank;
          });
        // .filter((item, index) => {
        //   return index < 100;
        // })
        // .sort((a, b) => {
        //   return a.sortOrder - b.sortOrder;
        // });
        setWantedTaxaList(wantedTicks);
      }
      if (activeTab === 0) {
        const mappedTaxaList = tickListTableData?.ticks.map((item) => {
          const findTaxa = allTaxa?.find((taxa) => taxa.taxonId === item.taxonId);
          return { ...item, ...findTaxa } as unknown as TaxonDisplay;
        });
        mappedTaxaList.sort((a, b) => {
          if (sortByDate) {
            return state.sortListAsc ? +new Date(a.date) - +new Date(b.date) : +new Date(b.date) - +new Date(a.date);
          }
          return state.sortListAsc ? a.sortOrder - b.sortOrder : b.sortOrder - a.sortOrder;
        });
        setDisplayTaxaList(mappedTaxaList);
      }
    }
  }, [allTaxa, tickListTableData, wanted, sortByDate, state.sortListAsc, activeTab]);

  return (
    <>
      <Paper
        elevation={0}
        square
        sx={{
          mx: -2,
        }}
      >
        {tickList && <TickListName tickList={tickList} />}
        <Box
          sx={{
            borderBottom: 1,
            borderColor: 'divider',
          }}
        >
          <TickListTabs
            tabItems={[
              {
                id: 0,
                label: 'Arter',
              },
              {
                id: 1,
                label: 'Frvntade',
              },
              {
                id: 2,
                label: 'Jmfr',
              },
            ]}
            callback={handleTabChange}
          />
        </Box>
      </Paper>
      <TickListTabPanel value={activeTab} index={0}>
        {activeTab === 0 && displayTaxaList && <SortableTickList taxaList={displayTaxaList} />}
      </TickListTabPanel>
      <TickListTabPanel value={activeTab} index={1}>
        {activeTab === 1 && tickList && <WantedSpeciesList wantedSpeciesList={wantedTaxaList} tickListId={tickList.id} />}
      </TickListTabPanel>
      <TickListTabPanel value={activeTab} index={2}>
        {activeTab === 2 && tickList && <CompareTickLists currentTickList={tickList} />}
      </TickListTabPanel>
    </>
  );
};
