import React, { useEffect } from 'react';
import * as serviceWorker from './serviceWorkerRegistration';

export const ServiceWorkerWrapper = () => {
  const [showReload, setShowReload] = React.useState(false);
  const [appOffline, setAppOffline] = React.useState(false);
  const [waitingWorker, setWaitingWorker] = React.useState<ServiceWorker | null>(null);

  const onSWUpdate = (registration: ServiceWorkerRegistration) => {
    setShowReload(true);
    setWaitingWorker(registration.waiting);
  };

  const onSuccess = (registration: ServiceWorkerRegistration) => {
    console.log('success', registration);
  };
  const onOffline = () => {
    console.log('app is in offline mode');
    setAppOffline(true);
  };

  useEffect(() => {
    serviceWorker.register({ onUpdate: onSWUpdate, onSuccess, onOffline });
  }, []);

  const reloadPage = () => {
    waitingWorker?.postMessage({ type: 'SKIP_WAITING' });
    setShowReload(false);
    const form = document.createElement('form');
    form.method = 'POST';
    form.action = window.location.href;
    document.body.appendChild(form);
    form.submit();
  };

  return (
    <>
      <div>{appOffline && <h1>App is working in offline mode!</h1>}</div>
      <div>
        {showReload && (
          <>
            <span>Det finne en ny version av X! Tryck p knappen fr att uppdatera</span>
            <button type='button' onClick={reloadPage}>
              Uppdatera
            </button>
          </>
        )}
      </div>
    </>
  );
};
