import { List, ListItem, ListItemIcon, Typography, ListItemText } from '@mui/material';
import { WindowOutlined } from '@mui/icons-material';
import { SortListButton } from 'src/shared/components/atoms/sortListButton';
import { TaxonDisplay } from 'src/shared/models/taxonDisplay';
import { SortTickListEnum } from 'src/shared/state/state';
import { SpeciesNameFormatted } from './seciesNameFormatted';

type SortableTickListProps = {
  taxaList: TaxonDisplay[];
};

export const SortableTickList = (props: SortableTickListProps) => {
  const { taxaList } = props;
  return (
    <>
      <List
        sx={{
          mx: -2,
          bgcolor: 'background.paper',
          p: 0,
          '& .MuiListItemSecondaryAction-root': {
            right: 7,
          },
          '& li:nth-of-type(even)': { backgroundColor: 'rgba(0,0,0,0.025)' },
          '& li.custom': {
            pl: 0,
            pr: 1,
            py: 0.5,
            position: 'relative',
            borderBottom: '1px solid rgba(0, 0, 0, 0.12)',
            '& small.count': {
              fontFamily: 'inherit',
              pl: 1,
              pr: 2,
              minWidth: '3.2rem',
              flexShrink: 0,
              display: 'inline-flex',
              justifyContent: 'flex-end',
              textAlign: 'right',
              color: 'rgba(0,0,0,0.3)',
            },
            '& small.date': {
              position: 'absolute',
              right: '16px',
              top: '50%',
              transform: 'translateY(-50%)',
            },
            '& small': {
              fontWeight: 400,
              fontSize: '0.75rem',
              lineHeight: 1.66,
              letterSpacing: '0.03333em',
            },
          },
        }}
      >
        <ListItem
          dense
          divider
          sx={{
            p: 0,
            borderBottomWidth: 2,
          }}
          secondaryAction={<SortListButton label='Datum' sortKey={SortTickListEnum.Date} />}
        >
          <ListItemIcon sx={{ justifyContent: 'flex-end', pr: 2, pl: 1, minWidth: '3rem' }}>
            <Typography variant='caption' textAlign='right' sx={{ color: 'rgba(0,0,0,0.3)' }}>
              #
            </Typography>
          </ListItemIcon>
          <ListItemText
            primary={<SortListButton label='Artnamn' sortKey={SortTickListEnum.SpeciesName} />}
            sx={{
              p: 0,
            }}
          />
        </ListItem>
        {taxaList &&
          taxaList.map((item: TaxonDisplay, index: number) => (
            <li className='custom' key={item.taxonId}>
              <small className='count'>{index + 1}</small>
              <SpeciesNameFormatted name={item.name} rank={item.rank} />
              <small className='date'>{item.date.toString()}</small>
            </li>
            // <ListItem
            //   key={item.taxonId}
            //   secondaryAction={<Typography variant='caption'>{item.date}</Typography>}
            //   dense
            //   divider
            //   sx={{
            //     pl: 0,
            //   }}
            // >
            //   <ListItemIcon sx={{ justifyContent: 'flex-end', pr: 2, pl: 1, minWidth: '2.5rem' }}>
            //     <Typography variant='caption' textAlign='right' sx={{ color: 'rgba(0,0,0,0.3)' }}>
            //       {index + 1}
            //     </Typography>
            //   </ListItemIcon>
            //   <ListItemText
            //     primary={
            //       <Typography
            //         variant='subtitle1'
            //         sx={{
            //           lineHeight: 1,
            //           fontWeight: item.rank < 15000 ? 500 : 300,
            //           color: item.rank < 1000 ? 'red' : 'inherit',
            //         }}
            //       >
            //         {item.name}
            //       </Typography>
            //     }
            //   />
            // </ListItem>
          ))}
      </List>
      {!taxaList ||
        (!taxaList.length && (
          <>
            <Typography
              variant='h4'
              textAlign='center'
              sx={{
                mt: 5,
                color: 'text.disabled',
                justifyContent: 'center',
                display: 'flex',
                flexWrap: 'wrap',
              }}
            >
              <span style={{ width: '100%' }}>Listan r tom</span>
              <WindowOutlined sx={{ fontSize: '5rem', mt: 1, opacity: 0.2 }} />
            </Typography>
          </>
        ))}
    </>
  );
};
