import { ListAlt, Star } from '@mui/icons-material';
import { List, ListItem, Typography, ListItemIcon, ListItemText } from '@mui/material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { db } from 'src/database';
import { TickList } from 'src/database/models/tickList';

type TagListOverviewProps = {
  tagId: number;
};

export const TagListOverview = (props: TagListOverviewProps) => {
  const { tagId } = props;

  const [tickLists, setTickLists] = useState<TickList[]>([]);

  const currentTagLists = useLiveQuery(() => db.tagLists.where('tagId').equals(tagId).toArray());
  const allLists = useLiveQuery(() => db.tickList.toArray());

  useEffect(() => {
    if (allLists && currentTagLists) {
      const lists = allLists.filter((x) => currentTagLists.some((list) => list.tickListId === x.id));
      setTickLists(lists);
    }
  }, [allLists, currentTagLists]);

  return (
    <List
      component='div'
      disablePadding
      sx={{
        borderTopWidth: 1,
        borderTopStyle: 'solid',
        borderTopColor: 'divider',
        '& .MuiListItemIcon-root': {
          minWidth: '2rem',
        },
        '& a': {
          color: 'text.primary',
        },
      }}
    >
      {tickLists
        .sort((a, b) => {
          return a.tickCount > b.tickCount ? 1 : -1;
        })
        .map((item: TickList, index: number) => {
          return (
            <ListItem
              key={item.id}
              secondaryAction={
                <Typography
                  component='span'
                  variant='body2'
                  sx={{
                    borderRadius: 1,
                    backgroundColor: 'secondary.main',
                    color: 'white',
                    px: 0.8,
                    py: 0.3,
                    fontWeight: 500,
                  }}
                >
                  {item.tickCount}
                </Typography>
              }
              component={Link}
              to={`/list/${item.id}`}
              divider={index !== tickLists.length - 1}
              dense
            >
              <ListItemIcon>
                <ListAlt />
              </ListItemIcon>
              <ListItemText
                primary={
                  <>
                    {item.listName} {item.year}
                    {item.favourite ? (
                      <Star
                        fontSize='small'
                        sx={{
                          ml: '5px',
                          mt: '2px',
                          fontSize: '14px',
                          verticalAlign: 'baseline',
                        }}
                      />
                    ) : null}
                  </>
                }
              />
            </ListItem>
          );
        })}
    </List>
  );
};
