import { Rating } from '@mui/material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { db } from 'src/database';
import { WantedTick } from 'src/database/models/wantedTick';
import { WantedTickList } from 'src/database/models/wantedTickList';

type WantedRatingProps = {
  tickListId: number;
  taxonId: number;
  rating: number;
  callback: () => void;
};

export const WantedRating = (props: WantedRatingProps) => {
  const { tickListId, taxonId, rating, callback } = props;
  const [value, setValue] = useState<number>(rating);
  const [tickExists, setTickExists] = useState<boolean>(false);

  const [wantedTickListTicks, setWantedTickListTicks] = useState<WantedTick[]>([]);

  const wantedTickList = useLiveQuery(() => db.wantedTickList.where('tickListId').equals(tickListId).toArray());

  useEffect(() => {
    if (wantedTickList !== undefined && wantedTickList[0]) {
      const tickExistsCheck = wantedTickList[0].ticks.some((item) => item.taxonId === taxonId);
      setWantedTickListTicks(wantedTickList[0].ticks);
      setTickExists(tickExistsCheck);
      if (tickExistsCheck) {
        setValue(wantedTickList[0].ticks.filter((x) => x.taxonId === taxonId)[0].rating);
      }
    }
  }, [wantedTickList]);

  const updateRating = (ratingValue: number | null) => {
    const newTickWithRating = { taxonId, rating: ratingValue ?? 0 } as WantedTick;
    if (wantedTickList && wantedTickList[0]) {
      const objIndex = wantedTickList[0].ticks.findIndex((obj) => obj.taxonId === taxonId);
      console.log('objIndex', objIndex);
      if (objIndex > -1) {
        wantedTickList[0].ticks[objIndex] = newTickWithRating;
      } else {
        wantedTickList[0].ticks.push(newTickWithRating);
      }
      toast.success(`Lade till arten som frvntad (${ratingValue})`);
      db.wantedTickList
        .put({
          id: wantedTickList[0].id,
          tickListId,
          ticks: wantedTickList[0].ticks,
        })
        .then(() => {
          callback();
        });
    } else {
      toast.success(`Lade till arten som frvntad (${ratingValue})`);
      db.wantedTickList
        .add({
          tickListId,
          ticks: [newTickWithRating],
        } as unknown as WantedTickList)
        .then(() => {
          callback();
        });
    }
  };

  return (
    <>
      {wantedTickListTicks && (
        <Rating
          name='simple-controlled'
          value={value}
          onChange={(event, newValue) => {
            updateRating(newValue);
          }}
        />
      )}
    </>
  );
};
