import { ListTypeEnum } from '../enums/listTypeEnum';
import { Player, SortTickListEnum } from './state';

export enum ActionType {
  ToggleListView,
  ToggleListDrawer,
  SortTickList,
  ExpandedTickListType,
  FocusQuantity,
  FocusSpeciesName,
  SetActiveTag,
  AddPlayer,
  SetPlayerValue,
  ResetGame,
}

export interface ToggleListView {
  type: ActionType.ToggleListView;
}

export interface ToggleListDrawer {
  type: ActionType.ToggleListDrawer;
}

export interface SortTickList {
  type: ActionType.SortTickList;
  payload: { sortBy: SortTickListEnum };
}

export interface ExpandedTickListType {
  type: ActionType.ExpandedTickListType;
  payload: { expandedTickListType: ListTypeEnum };
}

export interface SetActiveTag {
  type: ActionType.SetActiveTag;
  payload: { activeTagId: number | null };
}

export interface FocusQuantity {
  type: ActionType.FocusQuantity;
}

export interface FocusSpeciesName {
  type: ActionType.FocusSpeciesName;
}

export interface AddPlayer {
  type: ActionType.AddPlayer;
  payload: Player;
}

export interface SetPlayerValue {
  type: ActionType.SetPlayerValue;
  payload: { id: number; value: number };
}

export interface ResetGame {
  type: ActionType.ResetGame;
}

export type GameActions =
  | ToggleListView
  | ToggleListDrawer
  | SortTickList
  | ExpandedTickListType
  | SetActiveTag
  | FocusQuantity
  | FocusSpeciesName
  | AddPlayer
  | SetPlayerValue
  | ResetGame;
