import { ExpandMore, Delete } from '@mui/icons-material';
import { Button, ListItemIcon, Menu, MenuItem } from '@mui/material';
import { db } from 'src/database';
import React, { useContext } from 'react';
import { toast } from 'react-toastify';
import { DeleteConfirmationDialog } from 'src/shared/components/organisms/deleteConfirmationDialog';
import { setActiveTagId } from 'src/shared/state/reducer';
import { GameContext } from 'src/shared/state/context';

type TagsActionMenuProps = {
  tagId: number;
};

export const TagsActionMenu = (props: TagsActionMenuProps) => {
  const { tagId } = props;
  const { state, dispatch } = useContext(GameContext);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);

  console.log('tagId', tagId);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const [deleteId, setDeleteId] = React.useState<string | null>(null);

  const showDeleteConfirmationDialog = () => {
    setDeleteId(`${tagId}`);
  };

  const handleCloseDialog = async (newValue: any) => {
    setDeleteId(null);

    if (newValue) {
      toast.success('Taggen raderades');
      console.log(newValue, 'taggen', tagId);
      await db.tags
        .where('id')
        .equals(+tagId)
        .delete()
        .then(function (deleteCount) {
          console.log(`Deleted ${deleteCount} objects`);
        });
      await db.tagLists
        .where('tagId')
        .equals(+tagId)
        .delete();
      dispatch(setActiveTagId(null));
    }
  };

  return (
    <>
      <DeleteConfirmationDialog
        title='Vill du radera taggen?'
        modaltext='Listorna finns kvar...'
        open={!!deleteId}
        onClose={handleCloseDialog}
        value={deleteId}
      />
      <Button
        variant='outlined'
        color='primary'
        size='small'
        sx={{
          padding: 0,
          minWidth: '2rem',
          height: '2rem',
          lineHeight: 1,
          color: 'text.secondary',
          borderColor: '#ccc',
        }}
        id='basic-button'
        aria-controls='basic-menu'
        aria-haspopup='true'
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        <ExpandMore />
      </Button>
      <Menu
        id='basic-menu'
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {/* <MenuItem onClick={() => showEditTagDialog()}>
          <ListItemIcon>
            <Edit fontSize='small' />
          </ListItemIcon>
          Redigera tagg
        </MenuItem> */}
        <MenuItem onClick={() => showDeleteConfirmationDialog()}>
          <ListItemIcon>
            <Delete fontSize='small' />
          </ListItemIcon>
          Radera taggen
        </MenuItem>
      </Menu>
    </>
  );
};
