import { useEffect } from 'react';
import { useLiveQuery } from 'dexie-react-hooks';
import taxaList from 'src/database/data/taxaList.json';
import ConsoleHelper from 'src/shared/helpers/consoleHelper';
import { db } from 'src/database/index';

export const InitDatabase = () => {
  const taxonCount = useLiveQuery(() => db.taxon.count());

  useEffect(() => {
    if (taxonCount !== undefined && taxonCount === 0) {
      db.taxon.bulkPut(taxaList).then((lastKey) => {
        ConsoleHelper.log(`Taxon list success, last id was: ${lastKey}`);
      });
    }
  }, [taxonCount]);

  return null;
};
