import { IconButton, Box, Paper, Divider, Typography } from '@mui/material';
import { CheckBoxOutlineBlank, CheckBox } from '@mui/icons-material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useCallback, useEffect, useState } from 'react';
import { db } from 'src/database';
import { Taxon } from 'src/database/models/taxon';
import { TickList } from 'src/database/models/tickList';
import { Tick } from 'src/database/models/tick';
import { ToggleUniqueButton } from './toggleUniqueButton';
import { SpeciesNameFormatted } from './seciesNameFormatted';
import { EditTick } from './editTick';

type CompareGridProps = {
  currentTickList: TickList;
  tickListsToCompare: TickList[];
};

interface CompareTaxon extends Taxon {
  hasTick: boolean[];
  isUnique: boolean;
}

interface ListMap {
  id: number;
  name: string;
  count: number;
}

export const CompareGrid = (props: CompareGridProps) => {
  const { currentTickList, tickListsToCompare } = props;

  const [taxonId, setTaxonId] = useState<number | null>(0);
  const [tickListId, setTickListId] = useState<number | null>(0);

  const handeCloseTickEdit = useCallback(() => {
    setTaxonId(null);
  }, []);

  const [compareTickLists, setCompareTickLists] = useState<CompareTaxon[]>([]);
  const [showUnique, setShowUnique] = useState<boolean>(false);

  const listMap: ListMap[] = [
    {
      id: currentTickList.id,
      name: currentTickList.listName,
      count: currentTickList.ticks.length,
    },
    ...tickListsToCompare.map((x) => {
      return { id: x.id, name: x.listName, count: x.ticks.length } as ListMap;
    }),
  ];

  //   const mergedTickLists = currentTickList.ticks.concat(...tickListsToCompare.map((x) => x.ticks));
  //   console.log('merged', mergedTickLists);

  const allTaxa = useLiveQuery(() => db.taxon.toArray());

  const editCurrentTick = (hasTick: boolean, id: number, listId: number) => {
    console.log('editCurrentTick', hasTick, id, listId);
    if (currentTickList.id === listId && !hasTick && !currentTickList.ticks.some((x) => x.taxonId === id)) {
      currentTickList.ticks.push({
        taxonId: id,
        date: new Date().toISOString().substring(0, 10),
      } as unknown as Tick);
      db.tickList
        .update(listId, {
          ticks: currentTickList.ticks,
          tickCount: currentTickList.ticks.length,
          updated: new Date(),
        })
        .then(function (updated) {
          if (updated) console.log('Friend number 2 was renamed to Number 2');
          else console.log('Nothing was updated - there were no friend with primary key: 2');
        });
    } else {
      setTaxonId(id);
      setTickListId(listId);
    }
  };

  useEffect(() => {
    if (allTaxa !== undefined) {
      const tempCompareTickLists: CompareTaxon[] = [];
      allTaxa.forEach((item) => {
        const hasTickArray = [];
        hasTickArray.push(!!currentTickList.ticks.filter((x) => x.taxonId === item.taxonId).length);
        tickListsToCompare.forEach((list) => {
          hasTickArray.push(!!list.ticks.filter((x) => x.taxonId === item.taxonId).length);
        });

        if (hasTickArray.includes(true)) {
          tempCompareTickLists.push({ ...item, hasTick: hasTickArray, isUnique: hasTickArray.includes(false) });
        }
      });
      setCompareTickLists(tempCompareTickLists);
    }
  }, [allTaxa, currentTickList]);

  const toggleUnique = useCallback((state: boolean) => {
    console.log('toggleUnique', state);
    setShowUnique(state);
  }, []);

  const showEditDailog = tickListId !== null && taxonId !== null;

  return (
    <>
      {showEditDailog && <EditTick tickListId={tickListId} taxonId={taxonId} callback={handeCloseTickEdit} />}
      <Paper
        sx={{
          overflow: 'hidden',
          '& table': {
            borderCollapse: 'collapse',
            borderSpacing: 0,
            width: '100%',
          },
          '& .speciesName': {
            width: '100%',
            verticalAlign: 'middle',
          },
          '& td.count': {
            textAlign: 'right',
            fontSize: '0.6rem',
            color: 'text.disabled',
            verticalAlign: 'middle',
          },
          '& th': {
            p: 0,
          },
          '& tbody tr:nth-of-type(even)': { backgroundColor: 'rgba(0,0,0,0.025)' },
          '& td': {
            p: '1px 4px',
            verticalAlign: 'top',
            borderTopWidth: 1,
            borderTopStyle: 'solid',
            borderTopColor: 'divider',
            borderRight: '1px solid #eee',
          },
          '& .MuiIconButton-root': {
            p: 0,
            minWidth: '1.5rem',
            m: 0,
            borderRadius: 0,
            border: 'none',
          },
          '& .MuiSvgIcon-root': {
            fontSize: '1.6rem',
          },
          '& .buttonwrapper': {
            verticalAlign: 'bottom',
            textAlign: 'right',
          },
        }}
      >
        <table>
          <thead>
            <tr>
              {!showUnique && <th>&nbsp;</th>}
              {listMap.map((item) => (
                <th key={item.id} className='rotate'>
                  <div>
                    <span>
                      {item.name} <small> - {item.count}</small>
                    </span>
                  </div>
                </th>
              ))}
              <th className='buttonwrapper'>
                <ToggleUniqueButton callback={toggleUnique} />
              </th>
            </tr>
          </thead>
          <tbody>
            {compareTickLists &&
              compareTickLists.map((item, parentIndex) => {
                if (showUnique && !item.isUnique) {
                  return null;
                }
                return (
                  <tr key={item.taxonId}>
                    {!showUnique && <td className='count'>{parentIndex + 1}</td>}
                    {item.hasTick.map((x, index) => (
                      // variant={x ? 'contained' : 'outlined'}
                      // eslint-disable-next-line react/no-array-index-key
                      <td key={`${index}`}>
                        <IconButton
                          color={x ? 'success' : 'primary'}
                          size='small'
                          // disabled={!x}
                          onClick={() => editCurrentTick(x, item.taxonId, listMap[index].id)}
                        >
                          {x && <CheckBox />}
                          {!x && <CheckBoxOutlineBlank />}
                        </IconButton>
                      </td>
                    ))}
                    <td className='speciesName'>
                      <SpeciesNameFormatted name={item.name} rank={item.rank} />
                    </td>
                  </tr>
                );
              })}
          </tbody>
        </table>
      </Paper>
    </>
  );
};
