import { AddCircle, InfoOutlined } from '@mui/icons-material';
import { Button, Paper, Chip, Typography } from '@mui/material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useState, useCallback, useEffect, useContext } from 'react';
import { db } from 'src/database';
import { Tag } from 'src/database/models/tag';
import { GameContext } from 'src/shared/state/context';
import { setActiveTagId } from 'src/shared/state/reducer';
import { TagCreateForm } from './tagCreateForm';

export const TagEdit = () => {
  const { state, dispatch } = useContext(GameContext);
  const [showAddTagForm, setShowAddTagForm] = useState<boolean>(false);
  const toggleAddTagForm = useCallback(() => {
    console.log('here');
    setShowAddTagForm(false);
  }, []);

  const [tags, setTags] = useState<Tag[]>([]);

  const allTags = useLiveQuery(() => db.tags.toArray());
  useEffect(() => {
    if (allTags !== undefined) {
      setTags(allTags);
    }
  }, [allTags]);

  const changeActiveTag = (newActiveTagId: number) => {
    dispatch(setActiveTagId(newActiveTagId));
  };

  return (
    <>
      {showAddTagForm && <TagCreateForm callback={toggleAddTagForm} tag={null} />}
      {!showAddTagForm && (
        <>
          <Paper sx={{ p: !tags.length ? 0 : 2 }}>
            {tags &&
              tags.map((item: Tag) => (
                <Chip
                  label={item.name}
                  key={item.id}
                  sx={{
                    backgroundColor: item.color,
                    mr: 0.5,
                    mb: 0.5,
                    color: 'white',
                    fontWeight: 600,
                  }}
                  onClick={() => changeActiveTag(item.id)}
                />
              ))}
            {!tags.length && (
              <Typography variant='h6' sx={{ py: 1, px: 2, display: 'flex', alignItems: 'center' }}>
                <InfoOutlined fontSize='small' sx={{ color: 'text.disabled', mr: 1 }} />
                Taggar saknas
              </Typography>
            )}
          </Paper>
          <Button
            size='large'
            color='primary'
            variant='contained'
            sx={{
              mt: 2,
              width: '100%',
            }}
            onClick={() => setShowAddTagForm(true)}
            startIcon={<AddCircle />}
          >
            Skapa ny tagg
          </Button>
        </>
      )}
    </>
  );
};
