import { useState } from 'react';
import { db } from 'src/database';
import { TickList } from 'src/database/models/tickList';
import { Box, Button, Grid, Paper, TextField, Typography } from '@mui/material';
import { CloudUpload, DownloadingOutlined } from '@mui/icons-material';
import { toast } from 'react-toastify';
import { CopyInstructions } from '../components/copyInstructions';

export const ImportListPage = () => {
  const [listData, setListData] = useState<string>('');

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setListData(event.target.value);
  };

  const parseListData = async () => {
    const json = JSON.parse(listData);
    const year = Object.prototype.hasOwnProperty.call(json, 'year') ? json.year : null;
    const tickList = {
      listName: json.areaName,
      listType: json.type,
      year,
      ticks: json.speciesList,
      tickCount: json.speciesList.length,
      updated: new Date(),
      created: new Date(),
      imported: true,
      favourite: false,
      apUrl: json.apUrl,
    } as unknown as TickList;

    await db.transaction('rw', db.tickList, async function () {
      const result = await db.tickList.add(tickList);
      toast.success('Listan importerades');
      setListData('');
    });
  };

  return (
    <>
      <CopyInstructions />
      <Paper sx={{ p: 2 }}>
        <Grid container>
          <Grid item xs={12}>
            <Box sx={{ display: 'flex' }}>
              <DownloadingOutlined fontSize='small' sx={{ color: 'text.disabled', mr: 1, mt: '1px' }} />
              <Typography sx={{ fontWeight: 500, mb: 3 }}>Importa en lista frn Artportalen</Typography>
            </Box>
            <TextField
              sx={{ mb: 1 }}
              fullWidth
              multiline
              label='Importera lista'
              value={listData}
              placeholder='Klistra in listdata'
              onChange={handleChange}
              InputLabelProps={{ shrink: true }}
              rows={7}
              autoFocus
            />
          </Grid>
          <Grid item xs={12} textAlign='right'>
            <Button variant='contained' disabled={!listData} onClick={() => parseListData()} endIcon={<CloudUpload />}>
              Importera
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </>
  );
};
