import { Chip, Typography } from '@mui/material';
import { useLiveQuery } from 'dexie-react-hooks';
import { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { db } from 'src/database';
import { Tag } from 'src/database/models/tag';
import { TagList } from 'src/database/models/tagList';

type EditTickListTagsProps = {
  tickListId: number;
};

export interface TagWithList extends Tag {
  hasCurrentList: boolean;
}

export const EditTickListTags = (props: EditTickListTagsProps) => {
  const { tickListId } = props;
  const [tags, setTags] = useState<TagWithList[]>([]);

  const currentTagLists = useLiveQuery(() => db.tagLists.where('tickListId').equals(tickListId).toArray());
  const allTags = useLiveQuery(() => db.tags.toArray());
  useEffect(() => {
    if (allTags !== undefined) {
      const mapptedTags = allTags.map((x) => {
        return { ...x, hasCurrentList: !!currentTagLists?.filter((tag) => tag.tagId === x.id).length };
      });
      setTags(mapptedTags);
    }
  }, [allTags, currentTagLists]);

  const toggleTag = async (tag: TagWithList) => {
    console.log('toggleTag', tag.id, tag.hasCurrentList, tickListId);
    if (tag.hasCurrentList) {
      await db.tagLists
        .where('tickListId')
        .equals(tickListId)
        .and((x) => x.tagId === tag.id)
        .delete();
    } else {
      await db.tagLists.add({ tagId: tag.id, tickListId } as unknown as TagList);
    }
  };

  return (
    <>
      <Typography variant='caption' component='div'>
        Vlj en eller flera taggar ({tags.filter((x) => x.hasCurrentList).length} valda)
      </Typography>
      {tags &&
        tags.map((item: TagWithList) => (
          <Chip
            label={item.name}
            key={item.id}
            sx={{
              backgroundColor: item.color,
              mr: 0.5,
              mb: 0.5,
              color: 'white',
              fontWeight: 600,
            }}
            {...(item.hasCurrentList ? { onDelete: () => toggleTag(item) } : null)}
            onClick={() => toggleTag(item)}
          />
        ))}
    </>
  );
};
