import { Paper, Grid, Button, TextField } from '@mui/material';
import { useState, useCallback } from 'react';
import { toast } from 'react-toastify';
import { db } from 'src/database';
import { Tag } from 'src/database/models/tag';
import { TagColorPicker } from './tagColorPicker';

type TagCreateFormProps = {
  callback: () => void;
  tag?: Tag | null;
};

export const TagCreateForm = (props: TagCreateFormProps) => {
  const { callback, tag } = props;
  const [currentTag, setCurrentTag] = useState<Tag>(tag ?? ({ name: '', color: '' } as unknown as Tag));
  const changeTagName = (event: React.ChangeEvent<HTMLInputElement>) => {
    setCurrentTag((prev) => ({ ...prev, name: event.target.value as string }));
  };

  const changeTagColor = useCallback((newColor: string) => {
    setCurrentTag((prev) => ({ ...prev, color: newColor }));
  }, []);

  const handleCancel = () => {
    callback();
  };

  const handleOk = () => {
    if (currentTag.id) {
      db.tags
        .update(currentTag.id, {
          name: currentTag.name,
          color: currentTag.color,
        })
        .then(() => {
          toast.success('Taggen uppdaterades');
          callback();
        });
    } else {
      db.tags
        .add({
          name: currentTag.name,
          color: currentTag.color,
        } as unknown as Tag)
        .then(() => {
          toast.success('Taggen sparades');
          callback();
        });
    }
  };

  return (
    <Paper sx={{ p: 2, mt: 2 }}>
      <Grid container>
        <Grid item xs={12}>
          <TextField fullWidth label='Taggnamn' variant='outlined' size='small' value={currentTag.name} onChange={changeTagName} sx={{ mb: 2 }} />
        </Grid>
        <Grid item xs={12}>
          <TagColorPicker callback={changeTagColor} color={currentTag.color} />
        </Grid>
        <Grid item xs={12} textAlign='right'>
          <Button autoFocus onClick={handleCancel} color='primary' sx={{ mr: 2 }}>
            Avbryt
          </Button>
          <Button onClick={handleOk} color='primary' variant='contained'>
            Spara
          </Button>
        </Grid>
      </Grid>
    </Paper>
  );
};

TagCreateForm.defaultProps = {
  tag: null,
};
